

<template>
		
	<div id="">

		<h1> - </h1>
		<p></p>
		<Table stripe :columns="columns1" :data="data1"></Table>

	</div>

</template>
<script>
	export default {
		name: "yuan",
		data() {
			return {

				columns1: [{
						title: '',
						key: 'name',
						width: '100px'
					},
					{
						title: '',
						key: 'address'
					},

				],
				data1: [{
						name: '\\',
						address: ' "n " "n""\\n"  "\\\"  "\\"  "\\("  "("'
					},
					{
						name: '^',
						address: " RegExp  Multiline ^  '\\'  '\\r ' "
					},
					{
						name: '$',
						address: "RegExp  Multiline $  '\\n '  '\\r ' "
					},
					{
						name: '*',
						address: 'zo*  "z"  "zoo"* {0,}'
					},
					{
						name: '+',
						address: '"zo + "  "zo "  "zoo " "z "+  {1,}'
					},
					{
						name: '?',
						address: '"do(es)?"  "do"  "does" ?  {0,1}'
					},
					{
						name: '{n}',
						address: 'n  n "o{2}"  "Bob"  "o" "food"  o'
					},
					{
						name: '{n,}',
						address: "n n 'o{2,}'  'Bob ' 'o' 'foooood ' o'o{1,}'  'o+''o{0,}'  'o*'"
					},
					{
						name: '{n,m}',
						address: 'm  n n <= m n  m "o{1,3}"  "fooooood"  o"o{0,1}"  "o?"'
					},
					{
						name: '?',
						address: ' (*, +, ?, {n}, {n,}, {n,m})  "oooo ""o + ? "  "o " "o + "  "o "'
					},
					{
						name: '.',
						address: ' "\n"  "\n ""(.|\n)"'
					},
					{
						name: '(pattern)',
						address: ' pattern  Matches VBScript  SubMatches JScript  $0$9  "\( " "\)"'
					},
					{
						name: '(?:pattern)',
						address: ' pattern  ""  (|) " industr(?:y|ies)"  "industry|industries "'
					},
					{
						name: '(?=pattern)',
						address: ' pattern "Windows (?=95|98|NT|2000)"  "Windows 2000"  "Windows"  "Windows 3.1"  "Windows"'
					},
					{
						name: '(x|y)',
						address: ' x  y"z|food " "z"  "food""(z|f)ood " "zood"  "food"'
					},
					{
						name: '[xyz]',
						address: ' "[abc] " "plain"  "a"'
					}

					,
					{
						name: '[^xyz]',
						address: ' "[^abc]"  "plain" "p""l""i""n"'
					},
					{
						name: '[a-z]',
						address:"'[a-z]'  'a'  'z' "
					},
					{
						name: '[^a-z]',
						address: "'[^a-z]'  'a'  'z' "
					},
					{
						name: '\b',
						address: " 'er\b' 'never'  'er' 'verb' 'er'"
					},
					{
						name: '\B',
						address: "'er\B'  'verb ' 'er'' never'  'er'"
					},
					{
						name: '\\cx',
						address: " x  \\cM  Control-M x  A-Z  a-z  c  'c' "
					},
					{
						name: '\\d',
						address: " [0-9]"
					},
					{
						name: '\\D',
						address: " [^0-9]"
					},
					{
						name: '\\f',
						address: " \\x0c  \\cL"
					},
					{
						name: '\\n',
						address: " \\x0a  \\cJ"
					},
					{
						name: '\\r',
						address: " \\x0d  \\cM"
					},
					{
						name: '\\s',
						address: " [ \\f\\n\\r\\t\\v]"
					},
					{
						name: '\\S',
						address: " [^ \\f\\n\\r\\t\\v]"
					},
					{
						name: '\\t',
						address: " \\x09  \\cI"
					},
					{
						name: '\\v',
						address: " \\x0b  \\cK"
					},
					{
						name: '\\w',
						address: "'[A-Za-z0-9_]'"
					},
					{
						name: '\\W',
						address: " '[^A-Za-z0-9_]'"
					}
				,
					{
						name: '\\xn',
						address: " n n '\\x41'  'A''\\x041'  '\\x04' & '1' ASCII "
					}
				,
					{
						name: '\\num',
						address: " num num '(.)\\1' "
					}
				,
					{
						name: '\\n',
						address: "\\n  n  n  n  (0-7) n "
					}
				,
					{
						name: '\\nm',
						address: " \\nm  nm  nm  \\nm  n  n  m  n  m  (0-7) \\nm  nm"
					}
				,
					{
						name: '\\nml',
						address: " n  (0-3) m  l  (0-7) nml"
					}
				,
					{
						name: '\\un',
						address: " n n  Unicode  \\u00A9  (?)"
					}
				]
			}

		}
	}
</script>
<style type="text/css">
	* {
		font-size: 14px;
	}
	
	p {
		text-indent: 20px;
		line-height: 25px;
	}
	/*.layout-content-main{
		overflow: hidden;
		height: 1000px;
	}*/
	
	ul {
		list-style: square inside;
	}
</style>


