<template>
	<div class="layout-content-main" id="regExp">
		<h1> - </h1>
		<p>(regular expression)pattern 
		</p>
		<ul>
			<li>runoo+b runoobrunooobrunoooooob + 1</li>
			<li>runoo*b runobrunoobrunoooooob * 01</li>
			<li>colou?r  color  colour? 01</li>
		</ul>
		<p>
			
		</p>
		<p>
			 a  z""
		</p>
	<h1></h1>
	<p></p>
	<h1></h1>
    <Table stripe :columns="columns1" :data="data1"></Table>
    <!---->
    <h1></h1>
    <p> runoo*b  * *  *  \: runo\*ob  runo*ob
""\ </p>
    <Table stripe :columns="columns2" :data="data2"></Table>



	</div>

</template>
<script>
	export default {
		name: "grammer",
		data () {
            return {
            	
                columns1: [
                    {
                        title: '',
                        key: 'name',
                        width:'100px'
                    },
                    {
                        title: '',
                        key: 'address'
                    },
           
                ],
                data1: [
                    {
                        name: '\\cx',
                        address: 'x\\cM  Control-M x  A-Z  a-z  c " c" '
                    },
                    {
                        name: '\\f',
                        address: '\\x0c  \\cL'
                    },
                    {
                        name: '\\n',
                        address: ' \\x0a  \\cJ'
                    },
                    {
                        name: '\\r',
                        address: ' \\x0d  \\cM'
                    },
                    {
                        name: '\\s',
                        address: ' [ \\f\\n\\r\\t\\v]'
                    },
                    {
                        name: '\\S',
                        address: ' [^ \\f\\n\\r\\t\\v]'
                    }
                ],
                columns2: [
                    {
                        title: '',
                        key: 'name',
                        width:'100px'
                    },
                    {
                        title: '',
                        key: 'address'
                    },
           
                ],
                data2: [
                    {
                        name: '$',
                        address: ' RegExp  Multiline  $  "\\n " "\\r" $  \\$'
                    },
                    {
                        name: '( )',
                        address: ' \\(  \\)'
                    },
                    {
                        name: '\\n',
                        address: ' \\x0a  \\cJ'
                    },
                    {
                        name: '\\r',
                        address: ' \\x0d  \\cM'
                    },
                    {
                        name: '\\s',
                        address: ' [ \\f\\n\\r\\t\\v]'
                    },
                    {
                        name: '\\S',
                        address: ' [^ \\f\\n\\r\\t\\v]'
                    }
                ]
            }
        }
		
	}
</script>
<style type="text/css">
	*{
		font-size: 14px;
	}
	
	p{
		text-indent: 20px;
		line-height: 25px;
	}
	/*.layout-content-main{
		overflow: hidden;
		height: 1000px;
	}*/
	ul{
		list-style:square inside ;
	}
</style>


